//  * @description register builtin modules
//  *
//  */

// basic-modules
import '@klein-editor/basic-modules/dist/css/style.css';
import basicModules from '@klein-editor/basic-modules';

// list-module
import '@klein-editor/list-module/dist/css/style.css';
import wangEditorListModule from '@klein-editor/list-module';

// table-module
import '@klein-editor/table-module/dist/css/style.css';
import wangEditorTableModule from '@klein-editor/table-module';

// video-module
import '@klein-editor/video-module/dist/css/style.css';
import wangEditorVideoModule from '@klein-editor/video-module';

// upload-image-module
import '@klein-editor/upload-image-module/dist/css/style.css';
import wangEditorUploadImageModule from '@klein-editor/upload-image-module';

// code-highlight
import '@klein-editor/code-highlight/dist/css/style.css';
import { wangEditorCodeHighlightModule } from '@klein-editor/code-highlight';

import registerModule from './register';

basicModules.forEach((module) => registerModule(module));
registerModule(wangEditorListModule);
registerModule(wangEditorTableModule);
registerModule(wangEditorVideoModule);
registerModule(wangEditorUploadImageModule);
registerModule(wangEditorCodeHighlightModule);
